import React,{Component} from 'react';
import './login.css';
class Login extends Component{
  state={
    namel:"",
    NamelError:"",
    passl:"",
    PasslError:""
  };
  NamelChange = (event) => {
    this.setState({
      namel:event.target.value
    })
  }
  PasslChange = (event) => {
    this.setState({
      passl:event.target.value
    })
  }
  submit = ()=> {
    let error = 0;
    if(this.state.namel==="")
    {
      error=1;
      this.setState({
      NamelError:"Must Enter Inputs"
      })
    }
    if(this.state.passl==="")
    {
      error=1;
      this.setState({
      PasslError:"Must Enter Inputs"
      })
    }
    if(error===0)
    {
      this.props.start("logged");
      this.props.history.push('./');
    }
  }
  render(){
     return(
       <div className="bg">
       <div className="form">
         <label className="head">Login</label><br/>
         <label>User-ID:</label><input type="text" value={this.state.namel} onChange={this.NamelChange}/><br/><span>{this.state.NamelError}</span><br/>
         <label>Password:</label><input type="password" value={this.state.passl} onChange={this.PasslChange}/><br/><span>{this.state.PasslError}</span><br/>
         <button className="sum"  onClick={this.submit}> Submit</button>
       </div></div>
     );
   }
}
export default Login;
