import React,{Component} from 'react';
import '../App.css';
import Login from './login.js';
import Home from './home.js';
import Register from './register.js';
import Main from './main.js';
import Profile from './profile.js';
import Key from './key.js';
import {BrowserRouter,Route,Link} from 'react-router-dom';
class Header extends Component{
  constructor(props) {
      super(props);
      this.drop = false;
      this.css = "dis";
  }

  state={
    logged:[
      {
        name:"Login",
        link:"/login",
        class:"dis",
        drop:"jeevi"
      },
      {
        name:"Register",
        link:"/register",
        class:"dis"
      },
    ]
  };
 start = (e) =>
 {
   if(e==="logged")
   {
    this.setState({
      logged:[
        {
          name:"UserName",
          class:"dropdown-content",
          profile:"Profile",
          profilelink:"/profile",
          logout:"Logout",
          Key:"API-Key"
        },
        {
          name:"Widgets",
          link:"/",
          class:"dis"
        },
        {
          name:"Device",
          link:"/",
          class:"dis"
        },
        {
          name:"Dashboard",
          link:"/",
          class:"dis",
        }
      ]
    })
  }
}
  view = () => {
    return this.state.logged.map((logged,i) =>
    <div class="dropdown">
      <li key={i} ><Link to={logged.link} className="list dropdown">{logged.name}</Link></li>
       {this.menudrop(logged.name)}
      </div>
    );
  }
  menudrop = (e) => {
    if(e==="UserName")
    {
    return(
    <div class="dropdown-content">
    <Link to="/profile">Profile</Link><br/>
    <Link to="/key">API Key</Link><br/>
    <a href="/">Logout</a><br/></div>
  );}
  }
  key = () => {
    console.log("jeevi")
  }
  render(){
  return (
    <html>
    <meta name="viewport" content="width=device-width, initial-scale=1.0"></meta>
    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css"></link>
    <header className="App-header">
          <nav>
          <BrowserRouter>
            <input type="checkbox" id="check"></input>
            <label for="check" className="menu">
               <i class="fa fa-bars"></i>
            </label>
            <label className="title"><Link to="/"  style={{color:"white"}}>ITIOT Cloud</Link></label>
            <ul>
            {this.view()}
            </ul>
            <Route exact path="/login" render={(props) => <Login {...props} start={this.start} />}/>
            <Route exact path="/" component={Home}/>
            <Route exact path="/register" render={(props)=><Register {...props}  start={this.start}/>}/>
            <Route exact path="/main" component={Main}/>
            <Route exact path="/profile" component={Profile}/>
            <Route exact path="/key" component={Key}/>
            </BrowserRouter>
          </nav>
      </header>
      </html>
  );
}
}

export default Header;
